<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='readonly'
                       :showEdit="hasBtn('edit-email-template')"
                       :show_del="hasBtn('delete-email-template')"
                       :showReturn='false'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>

    <v-tabs v-model="tab"
            grow
            @change="tabchange"
            background-color="#eeeef0"
            color='primary'
            dark>
      <v-tab v-for="item in items"
             style="color:black"
             :key="item.tab">
        {{ item.tab }}
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item>
        <commoninfor :disabled='readonly'></commoninfor>
      </v-tab-item>

      <v-tab-item>

        <emailtemplate :disabled='readonly'></emailtemplate>
      </v-tab-item>

    </v-tabs-items>
  </div>
</template>

<script>
import commoninfor from './modules/commoninfor'
import emailtemplate from './modules/emailtemplate'
import { getemailtemplatelist, getdetailemailtemplate, deleteemailtemplate } from '@/service/api/emailtemplate'
export default {
  components: {
    commoninfor,
    emailtemplate
  },
  props: {},
  data () {
    return {
      readonly: false,
      tab: 0,
      items: [
        { tab: this.$t('label_The_general_information') },
        { tab: this.$t('label_The_template') },
      ],
      list: {}
    };
  },
  computed: {},
  created () { },
  watch: {},
  mounted () {
    this.readonly = this.$store.state.iconstatus
    this.$route.meta.title = this.$store.getters.subPage_name
    this.tab = this.$store.state.tab
  },

  methods: {
    tabchange () {
      this.$store.commit('setTab', this.tab)
    },
    handleEdit () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', this.readonly)
    },
    handleDel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
        deleteemailtemplate({ uuid: this.$store.state.uuid }).then(res => {
          if (res.code === '200') {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.push({
              name: 'emailtemplate'
            })
          }
        })
      })
    },
  },
};
</script>

